import React from 'react';
import './Navbar.css';
import Logo from '../Images/Logo.png';
import { useState, useEffect } from 'react';

const Navbar = () => {

    const [menu, setMenu] = useState(true);
    const [click, setClick] = useState(false);

    const handleClick = () => {
        setClick(!click);
    }

    const closeMobileMenu = () => {
        setClick(false);
    }

    const updateMenu = () => {
        if(window.innerWidth < 1000){
            setMenu(true);
        } else{
            setClick(false);
            setMenu(false);
        }
    }

    useEffect(() => {
        updateMenu();
        window.addEventListener("resize", updateMenu);
    }, [])

    const fakeAlert = () => {
        closeMobileMenu();
        alert("This feature will be available soon. Thank you for your patience.");
    }

    return (
        <div className = "navbar-container">
            <div className = "demo-logo">
                <img src={Logo} alt="Logo" className = "instantLogo" />
            </div>
            <div className = {menu? "menu-icon" : "menu-icon-hidden"} onClick = {handleClick} >
                <i className = {click ? 'fas fa-times' : 'fas fa-bars'} />
            </div>
            <div className = {click ? "menu-items-dropdown" : !menu ? "menu-items" : "menu-items-resp"}>
                <div className = "menu-item">
                    <a href = "https://uidai.gov.in/ecosystem/authentication-devices-documents/about-aadhaar-paperless-offline-e-kyc.html#:~:text=UIDAI%20provides%20a%20mechanism%20to,paper%20based%20verification%20and%20KYC."
                    target = "_blank">About the process</a>
                </div>
                <div className = "menu-item">
                    <a href = "https://knowyourcustomer.com/privacy-policy/" target = "_blank">Privacy Terms</a>
                </div>
                <div className = "menu-item">
                <a href = "https://coinmetro.com/blog/why-is-kyc-important/" target = "_blank">Why it is important?</a>
                </div>
            </div>
        </div>
    )
}

export default Navbar
